<template>
    <div class="p-20">
        <div class="filter-container" style="margin: 20px 10px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、用户名"
                      v-model="currencyTrans.keyword">
            </el-input>
            <el-button class="filter-item" @click="conditionQuery" type="primary" size="small" style="margin-left: 30px">查询
            </el-button>
        </div>
        <el-table
                ref="singleTable"
                :data="tableData">
            <el-table-column
                    type="index"
                    label="#">
            </el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="currencyTrans.page"
                           :page-sizes="[10,20,30, 50]" :page-size="currencyTrans.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {entitywallet} from '@/api/capital';

    export default {
        data() {
            return {
                total: 0,
                tableData: [],
                currencyTrans: {
                    keyword: '',
                    page: 1,
                    pageSize: 10
                },
                tableHeader: [{
                    label: "UID",
                    prop: "walletSeq",
                    width: 90
                }, {
                    label: "姓名",
                    prop: "realName",
                    width: 180
                }, {
                    label: "手机号码",
                    prop: "phoneNum",
                }, {
                    label: "邮箱",
                    prop: "email",
                    width: 100
                }, {
                    label: "虚拟币类型",
                    prop: "coinName",
                    width: 180,
                    align: 'center'
                }, {
                    label: "总金额",
                    prop: "cashAmount",
                    width: 180
                }, {
                    label: "挂单金额",
                    prop: "ordersAmount",
                }, {
                    label: "冻结金额",
                    prop: "freezeMcnt",
                    width: 180
                }, {
                    label: "可用金额",
                    prop: "availMoney",
                    width: 180
                },{
                    label: "最后修改时间",
                    prop: "updateTime",
                    width: 180
                }]
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },
        methods: {
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            },
            conditionQuery() {
                this.getList();
            },
            //查询所有列表以及条件查询
            getList() {
                entitywallet(this.currencyTrans).then(response => {
                    this.total = response.data.total;
                    this.tableData = response.data.list;
                    if (this.total <= 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                })
            }
        }, created() {
            this.getList();
        }
    }
</script>


